import React from 'react'

const AddImage = props => {

    let content = (
        <div>
            <div>
                <input
                    accept="image/*,image/heif,image/heic,video/*,video/mp4,video/x-m4v,video/x-matroska"
                    multiple
                    type="file"
                    onChange={(event) => {
                        console.log(event.target.value)//event.files[0].mozFullPath);
                    }}
                />
                <div role="button" tabindex="0">
                    <div>
                        <span>
                            <i role="img"></i>
                        </span>
                        <span dir="auto">
                            Photo/Video
                        </span>
                    </div>
                    <div data-visualcompletion="ignore">
                    </div>
                </div>
            </div>
        </div>
    )

    return content
}

export default AddImage